import { Component, OnInit } from '@angular/core';
import {animate, style, transition, trigger} from '@angular/animations';
import {DarkSideService} from '../dark-side.service';

@Component({
  selector: 'app-wish',
  templateUrl: './wish.component.html',
  styleUrls: ['./wish.component.css'],
  animations: [
    trigger(
      'inOutAnimation',
      [
        transition(
          ':enter',
          [
            style({ opacity: 0 }),
            animate('0.3s ease-out',
              style({ opacity: 1 }))
          ]
        ),
        transition(
          ':leave',
          [
            style({ opacity: 1 }),
            animate('0.3s ease-in',
              style({ opacity: 0 }))
          ]
        )
      ]
    )
  ]
})
export class WishComponent implements OnInit {

  sizes: any = {
    nasty: 0,
    oleg: 0
  };

  constructor(public darkSideService: DarkSideService) { }

  ngOnInit() {
    if (localStorage.getItem('nasty')) {
      this.sizes.nasty = localStorage.getItem('nasty');
    }
    if (localStorage.getItem('oleg')) {
      this.sizes.oleg = localStorage.getItem('oleg');
    }
  }

  plusSize(name: string) {
    this.sizes[name]++;
    localStorage.setItem(name, this.sizes[name]);
  }
  minusSize(name: string) {
    this.sizes[name]--;
    localStorage.setItem(name, this.sizes[name]);
  }
}
