import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class FilmsService {
  film = [];
  filmsId = ['tt0468569', 'tt1375666', 'tt0109830', 'tt0120689', 'tt0120689', 'tt0816692', 'tt1853728',
  'tt0910970', 'tt4154756', 'tt4633694', 'tt2380307', 'tt0361748', 'tt0180093', 'tt0457430', 'tt0469494',
  'tt0434409', 'tt1130884', 'tt2096673', 'tt1291584', 'tt3170832', 'tt1205489', 'tt3011894', 'tt0978762',
  'tt2267998', 'tt2119532', 'tt1950186', 'tt0405159', 'tt3315342', 'tt4016934', 'tt1895587', 'tt8613070',
  'tt0381681', 'tt4430212', 'tt1454029', 'tt4857264', 'tt6751668', 'tt8579674', 'tt0993846', 'tt1392190',
  'tt8946378', 'tt8367814', 'tt7131622', 'tt5606664'];

  randomNumber(min, max) {
    const rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
  }


  constructor(public http: HttpClient) {
    for (let i = 0; i < 5; i++) {
      const film = this.randomNumber(0, this.filmsId.length - 1);
      const url = `https://www.omdbapi.com/?apikey=5dcc54b1&i=${this.filmsId[film]}`;
      this.http.get(url).subscribe((item) => {
        this.film.push(item);
      });
    }
  }
}
