import { Component } from '@angular/core';
import {animate, style, transition, trigger} from '@angular/animations';
import {DarkSideService} from './dark-side.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    trigger(
      'inOutAnimation',
      [
        transition(
          ':enter',
          [
            style({ opacity: 0 }),
            animate('0.3s ease-out',
              style({ opacity: 1 }))
          ]
        ),
        transition(
          ':leave',
          [
            style({ opacity: 1 }),
            animate('0.3s ease-in',
              style({ opacity: 0 }))
          ]
        )
      ]
    )
  ]
})
export class AppComponent {
  title = 'surprise';

  selectedTab = {
    lohnessy: false,
    grafoman: false,
    film: false,
    wish: false,
    calendar: false,
    trueFalseGame: false
  };
  backToMain() {
    this.selectedTab.lohnessy = false;
    this.selectedTab.grafoman = false;
    this.selectedTab.film = false;
    this.selectedTab.wish = false;
    this.selectedTab.calendar = false;
    this.selectedTab.trueFalseGame = false;
    setTimeout(() => {
      window.scrollTo(0, 0);
    }, 100);
  }
  selectRoom(roomName: string) {
    this.selectedTab[roomName] = true;
    setTimeout(() => {
      window.scrollTo(0, 0);
    }, 100);
  }
  constructor(public darkSideService: DarkSideService) {}
}
