import { Component, OnInit } from '@angular/core';
import {TrueFalseService} from '../true-false.service';
import {DarkSideService} from '../dark-side.service';
import {animate, style, transition, trigger} from '@angular/animations';

@Component({
  selector: 'app-true-false',
  templateUrl: './true-false.component.html',
  styleUrls: ['./true-false.component.css'],
  animations: [
    trigger(
      'inOutAnimation',
      [
        transition(
          ':enter',
          [
            style({ opacity: 0 }),
            animate('0.3s ease-out',
              style({ opacity: 1 }))
          ]
        ),
        transition(
          ':leave',
          [
            style({ opacity: 1 }),
            animate('0.3s ease-in',
              style({ opacity: 0 }))
          ]
        )
      ]
    )
  ]
})
export class TrueFalseComponent implements OnInit {

  currentQuestion = 'Выбери правду или действие';

  constructor(public trueFalseService: TrueFalseService, public darkSideService: DarkSideService) { }

  ngOnInit() {
  }

  randomNumber(min, max) {
    const rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
  }

  selectQuestion() {
    this.currentQuestion = this.trueFalseService.questions[this.randomNumber(0, this.trueFalseService.questions.length - 1)];
  }

  selectAction() {
    this.currentQuestion = this.trueFalseService.actions[this.randomNumber(0, this.trueFalseService.actions.length - 1)];
  }

}
